import _ from 'lodash'
import {
  SEARCH_ORDERS,
  CREATE_ORDER,
  UPDATE_ORDER,
  DELETE_ORDER
} from '../actions/actionTypes'
import { DRINK_MAP } from '../../constants'

const getDrinkInfo = (key) => _.get(DRINK_MAP, key, {})

const calcOrdersPrice = orders => {
  return orders.reduce((total, { price }) => (total += price), 0)
}
export const defaultOrders = [
  {
    id: '0',
    key: 'green',
    name: 'Green tea',
    price: 20,
    notes: ''
  },
  {
    id: '1',
    key: 'black',
    name: 'Black tea',
    price: 30,
    notes: 'Suger free and Ice free \nplz.'
  }
]
const totalPrice = calcOrdersPrice(defaultOrders)

const initialState = {
  totalPrice,
  totalOrders: defaultOrders,
  currentOrders: []
}

const orderReducer = (state = initialState, action) => {
  switch (action.type) {
    case SEARCH_ORDERS: {
      const { id } = action.payload.order
      const newOrders = state.totalOrders.filter(({ id: stateOrderId }) => stateOrderId === id)
      return {
        ...state,
        currentOrders: newOrders || []
      }
    }
    case CREATE_ORDER: {
      const { drinkKey, notes } = action.payload.order
      const { price = 0, ...restInfo } = getDrinkInfo(drinkKey)
      const newOrders = [
        {
          notes,
          price,
          id: `${Date.now()}`,
          ...restInfo
        },
        ...state.totalOrders
      ]
      return {
        ...state,
        totalOrders: newOrders,
        totalPrice: state.totalPrice + price
      }
    }
    case UPDATE_ORDER: {
      const { id, drinkKey, notes } = action.payload.order
      const newOrders = state.totalOrders.map(stateOrder => {
        if (id === stateOrder.id) {
          return {
            ...stateOrder,
            ...getDrinkInfo(drinkKey),
            notes
          }
        }
        return stateOrder
      })
      return {
        ...state,
        totalOrders: newOrders,
        totalPrice: calcOrdersPrice(newOrders)
      }
    }
    case DELETE_ORDER: {
      const { id } = action.payload.order
      const newOrders = state.totalOrders.filter(({ id: stateOrderId }) => stateOrderId !== id)
      return {
        ...state,
        totalOrders: newOrders,
        totalPrice: calcOrdersPrice(newOrders)
      }
    }
    default:
      return state
  }
}

export default orderReducer
