export const DRINKS = [
  { name: 'Milk tea', price: 60, key: 'milk' },
  { name: 'Green tea', price: 20, key: 'green' },
  { name: 'Black tea', price: 30, key: 'black' }
]

export const DRINK_MAP = DRINKS.reduce((drinkMap, drink) => {
  drinkMap[drink.key] = drink
  return drinkMap
}, {})

