import React from 'react'
import clx from 'classnames'
import styles from './index.module.scss'

const Loader = ({ isLoading, text }) => {
  return (
    <div className={clx(
      styles.laderArea,
      { hidden: !isLoading }
    )}>
      <div className={styles.loader} />
      <p className={styles.loaderText}>{text}</p>
    </div>
  )
}

Loader.defaultProps = {
  isLoading: false,
  text: 'Loading...'
}

export default Loader
