import {
  SET_LOADING
} from '../actions/actionTypes'

const initialState = {
  isLoading: false
}

const commonReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_LOADING: {
      return {
        ...state,
        isLoading: action.payload.isLoading
      }
    }
    default:
      return state
  }
}

export default commonReducer
