import Loadable from 'react-loadable'
import Loader from './component/Loader'
import { MODIFY } from './constants'

const lazyloadComponent = (componentName) => {
  return Loadable({
    loader: () => import(`./container/${componentName}`),
    loading: Loader
  })
}

const routers = [
  {
    key: 'home',
    path: '/home',
    component: lazyloadComponent('OrderList')
  }, {
    key: MODIFY.CREATE,
    path: `/${MODIFY.CREATE}`,
    modifyType: MODIFY.CREATE,
    component: lazyloadComponent('ModifyOrder')
  }, {
    key: MODIFY.UPDATE,
    path: `/${MODIFY.UPDATE}/:orderId`,
    modifyType: MODIFY.UPDATE,
    component: lazyloadComponent('ModifyOrder')
  }, {
    key: MODIFY.DELETE,
    path: `/${MODIFY.DELETE}/:orderId`,
    modifyType: MODIFY.DELETE,
    component: lazyloadComponent('ModifyOrder')
  }, {
    key: 'error',
    component: lazyloadComponent('NotFound')
  }
]

export default routers
